import React, { Component } from 'react';
import E from 'wangeditor'

export default class Editor extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            editorContent: ''
        }
    }
    componentDidMount() {
        const elem = this.refs.editorElem
        const editor = new E(elem)
        const { setImages } = this.props
        // 
        editor.customConfig.menus = [
            'bold',  // 
            'fontSize',  // 
            'fontName',  // 
            'justify',  // 
            'quote',  // 
            'link', // 
            'emoticon',  // 
            'image',  // 
            'code',  // 
            'undo',  // 
        ]
        //  onchange  state 
        editor.customConfig.onchange = html => {
            this.setState({
                editorContent: html
            })
            this.props.onContentChange(html)
        }
        // 
        editor.customConfig.uploadImgServer = 'http://localhost:5001/upload'
        editor.customConfig.uploadImgHooks = {
            success: function (xhr, editor, result) {
                // 
                // xhr  XMLHttpRequst editor result 
                console.log(result.data)
                if (result.errno === 0) {
                    setImages(result.data)
                }
            },
        }
        editor.customConfig.uploadFileName = 'file'
        editor.customConfig.debug=true;
        //editor.customConfig.uploadImgShowBase64 = true   //  base64 
        editor.create()
    }
    clickHandle() {
        alert(this.state.editorContent)
    }
    render() {
        return (
            <div>
                {/* <div style={{width: "100%"}} dangerouslySetInnerHTML={{__html: this.state.editorContent}} /> */}
                {/*  */}
                <div ref="editorElem" style={{textAlign: 'left', width: "100%"}}/>
                {this.props.debug ? <button onClick={this.clickHandle.bind(this)}></button> : null}
            </div>
        );
    }
    
}